<template>
  <button
    class="btn"
    style=" width: 90px"
    :title="tooltipMessage"
    data-bs-toggle="tooltip"
  >
    <img
      :src="require(`@/assets/diablo2/images/rune/${rune.name}.png`)"
      style="width:40px;height:40px"
    />
    <br />
    {{ rune.nameKo }}
  </button>
</template>

<script>
import { mapGetters } from "vuex";

import { Tooltip } from "bootstrap/dist/js/bootstrap.esm.min.js";

export default {
  props: { id: Number },
  data() {
    return {
      rune: {},
    };
  },

  computed: {
    ...mapGetters("diablo2/rune", ["getRuneById"]),
    tooltipMessage() {
      var message =
        "<span class='title'>" +
        this.rune.nameKo +
        " 룬</span><br/>홈이 있는 아이템에 삽입 가능<br /><br />" +
        this.rune.option +
        "<br /><br />요구 레벨: " +
        this.rune.level +
        (this.rune.upgrade == ""
          ? ""
          : "<br />업그레이드 : " + this.rune.upgrade);
      return message;
    },
  },
  created: function() {
    this.rune = this.getRuneById(this.id);
  },
  methods: {},
  mounted() {
    new Tooltip(this.$el, {
      html: true,
      trigger: "hover",
      delay: { show: 0 },
    });
  },
};
</script>

<style>
.tooltip-inner {
  max-width: 300px;
  padding: 10px 10px 10px 10px;
  background: #000000;
  color: rgba(255, 255, 255, 0.7);
  border: 3px solid #3b3b3b;
}
.tooltip-inner .title {
  color: #e7c56d;
}
</style>
